.user-modal {
    .user-form {
        .row {
            /* align-items: center; */

            .error-message {
                font-size: 14px;
                color: #ff0000bd;
            }

            .form-control.invalid {
                border-color: #ff5353c7;
            }

            .form-group__field {
                display: flex;

                .form-group__controls {
                    display: flex;
                }
            }

            .user-modal__btn {
                padding: 6px;

                .user-modal__btn-icon {}
            }

            .accounts-list {
                flex-wrap: wrap;
                margin-right: -10px;

                .accounts-list__item {
                    display: flex;
                    justify-content: space-between;
                    /* align-items: center; */
                    position: relative;
                    flex: 1 1 auto;
                    border: 1px solid rgba(0, 0, 0, .125);
                    border-radius: .25rem;
                    padding: 10px;
                    margin-right: 10px;
                    margin-top: 10px;
                    overflow: auto;

                    .accounts-list__content {
                        overflow: auto;

                        .accounts-list__title {
                            font-weight: 600;
                            padding-right: 85px;
                        }

                        .accounts-list__text {}
                    }

                    .accounts-list__controls {
                        flex: 0 0 auto;
                        position: absolute;
                        top: 0;
                        right: 0;

                        .accounts-list__btn {
                            padding: 5px;

                            .accounts-list__btn-icon {
                                font-size: 20px;
                            }
                        }
                    }
                }

                .accounts-list__item--active {}

                .accounts-list__item--closed {
                    background-color: #ddd;
                }
            }
        }
    }

}

@media (max-width: 767px) {
    .user-modal {
        .user-modal__btn {
            padding: 6px;

            .user-modal__btn-icon {
                font-size: 22px;
            }
        }

        .accounts-list {
            margin-top: 5px;

            .accounts-list__item {}
        }
    }
}