$mainColor: #4c43a1;

button,
button:focus {
    outline: none;
}

.custom-toolbar {
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
    padding: 5px;

    .custom-selected-rows {
        display: flex;
        align-items: center;
        flex-wrap: nowrap;
        padding-left: 12px;
    }

    .custom-selected-rows__text {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        color: $mainColor;
        padding-right: 1px;
    }

    .custom-selected-rows__btn {}

    .custom-toolbar__actions {
        display: flex;
        flex-wrap: nowrap;
        align-items: center;
        /* margin: 0 3px; */
    }

    .custom-search {
        display: flex;
        align-items: center;
        flex-wrap: nowrap;
        flex: 0 1 auto;
        padding: 6px 12px;
        /* padding-right: 12px;
        padding-left: 12px; */
    }

    .custom-search__input {
        margin-right: 12px;
    }

    .custom-search__btn {}

    .custom-toolbar__add-btn {
        margin: 6px 0;
    }

    .custom-toolbar__action {}

    .custom-toolbar__action-btn {}

    .user-tabs {
        margin-left: 15px;

        .user-tabs__btn {
            margin-right: 10px;
            text-transform: none;
        }

        .user-tabs__btn.active {
            background-color: $mainColor;
            border-color: $mainColor;
        }

        .user-tabs__btn:not(.active) {
            color: $mainColor;
            background-color: transparent;
            box-shadow: none;
        }

        .user-tabs__btn:hover {
            box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
        }
    }

    @media (min-width: 500px) {
        .custom-search__input {
            min-width: 335px;
        }
    }

    @media (max-width: 820px) {
        flex-wrap: wrap;
        justify-content: center;
    }
}